@import '/scss/Variables';
@import '/scss/Mixins';

a {
  outline: none;
  text-decoration: none;
}

.titled-paragraphs {
  font-family: $font-family;
  .title {
    font-weight: bold;
  }

  // Small screens
  @media (max-width: $breakpoint-medium - 1) {
    .title {
      @include fontSizeStyles($font-size-medium, $line-height-normal);
      margin-bottom: 20px;
    }
    .paragraphs {
      @include fontSizeStyles($font-size-small, $line-height-high);
    }
  }

  // Large screens
  @media (min-width: $breakpoint-medium) {
    .title {
      @include fontSizeStyles($font-size-large, $line-height-normal);
      margin-bottom: 10px;
    }
    .paragraphs {
      @include fontSizeStyles($font-size-medium, $line-height-high);
    }
  }
}

//=== Drawing effect

@mixin btn-border-drawing(
  $color: #ccc,
  $hover: black,
  $width: 2px,
  $vertical: top,
  $horizontal: left,
  $duration: 0.25s
) {
  box-shadow: inset 0 0 0 $width $color;
  color: $color;
  transition: color $duration $duration/3;
  position: relative;

  &::before,
  &::after {
    border: 0 solid transparent;
    box-sizing: border-box;
    content: '';
    pointer-events: none;
    position: absolute;
    width: 0;
    height: 0;

    #{$vertical}: 0;
    #{$horizontal}: 0;
  }

  &::before {
    $h-side: if($horizontal == 'left', 'right', 'left');

    border-#{$vertical}-width: $width;
    border-#{$h-side}-width: $width;
  }

  &::after {
    $v-side: if($vertical == 'top', 'bottom', 'top');

    border-#{$v-side}-width: $width;
    border-#{$horizontal}-width: $width;
  }

  &:hover {
    color: $hover;

    &::before,
    &::after {
      border-color: $hover;
      transition: border-color 0s, width $duration, height $duration;
      width: 100%;
      height: 100%;
    }

    &::before {
      transition-delay: 0s, 0s, $duration;
    }

    &::after {
      transition-delay: 0s, $duration, 0s;
    }
  }
}

.draw-border {
  @include btn-border-drawing($color-royal-blue, $color-red, 4px, bottom, right);
}

//=== Button styling, semi-ignore
.btn {
  outline: none;
  margin-top: 20px;
  background: none;
  border: none;
  cursor: pointer;
  line-height: 1.5;
  font: 700 1.2rem 'Roboto Slab', sans-serif;
  padding: 1em 2em;
  letter-spacing: 0.05rem;
}
